.loader {
  position: relative;
}

.loaderText {
  display: none;
}

.loading {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(230, 230, 230, 0.6);
  font-weight: bold;
  color: #2f804e;
}
